import React, {Component} from 'react';
import './ImageHover.css';
import { Container, Row, Col } from 'react-bootstrap';

//Pictures
import worldSkillsImageHover from "../ImageHoverHomePage/ImageHoverPics/worldSkillsImageHover.jpg";
import droneCetImageHover from "../ImageHoverHomePage/ImageHoverPics/droneCetImageHover.png";
import fypHandExoskeleton from "../ImageHoverHomePage/ImageHoverPics/fypHandExoskeleton.JPG";
import worldSkills2018ImageHover from "../ImageHoverHomePage/ImageHoverPics/worldSkills2018ImageHover.jpg";
import worldSkills2019TianJinImageHover from "../ImageHoverHomePage/ImageHoverPics/worldSkills2019TianJinImageHover.jpg";
import worldSkills2018BkkImageHover from "../ImageHoverHomePage/ImageHoverPics/worldSkills2018BkkImageHover.JPG";
import background from "../ImageHoverHomePage/ImageHoverPics/background.jpg";

export class ImageHover extends Component{
    render(){
        return(
            <div className="titleContainer">
                 <h3 className="imageHoverTitle">At a Glance</h3>
                 <hr className="horizontalLineImageHover"></hr>
                    <div className="anchorBackground" img src={background}> 
                        <div className="ImageContainer">
                            <div className="worldSkillsImageContainer">
                                <img src= {worldSkillsImageHover} className="worldSkillsImageHover"></img>
                                    <div className="middle">
                                        <div className="worldSkillsPara">World Skills Russia</div>
                                            <div className="worldSkillsParaTwo">Sed id commodo erat, nec rhoncus sapien.Sed id commodo erat, nec rhoncus sapien.Sed id commodo erat, nec rhoncus sapien.  </div>
                                                <div className="text">Continue Reading</div>
                                    
                                    </div>
                            </div>
                            <div className="droneCetImageContainer">
                                <img src={droneCetImageHover} className="droneCetImageHover"></img>
                                    <div className="middleDrone">
                                        <div className="DroneCetPara">Drone CET</div>
                                            <div className="DroneCetParaTwo">Sed id commodo erat, nec rhoncus sapien.Sed id commodo erat, nec rhoncus sapien.Sed id commodo erat, nec rhoncus sapien.  </div>
                                                <div className="textDrone">Continue Reading</div>
                                    
                                    </div>
                            </div>
                            <div className="FypHandContainer">
                                <img src={fypHandExoskeleton} className="fypHandExoskeletonImageHover"></img>
                                    <div className="middleFyp">
                                        <div className="FypPara">Hand Exoskeleton</div>
                                            <div className="FypParaTwo">Sed id commodo erat, nec rhoncus sapien.Sed id commodo erat, nec rhoncus sapien.Sed id commodo erat, nec rhoncus sapien.  </div>
                                                <div className="textFyp">Continue Reading</div>
                                    
                                    </div>
                            </div>
                            <div className="worldSkills2018Container">
                                <img src={worldSkills2018ImageHover} className="worldSkills2018ImageHover"></img>
                                    <div className="middleWorldSkills2018">
                                        <div className="WorldSkills2018Para">World Skills 2018</div>
                                            <div className="WorldSkills2018ParaTwo">Sed id commodo erat, nec rhoncus sapien.Sed id commodo erat, nec rhoncus sapien.Sed id commodo erat, nec rhoncus sapien.  </div>
                                                <div className="textWorldSkills2018">Continue Reading</div>
                                    
                                    </div>
                            </div>
                        {/*<div className="worldSkills2019Container">
                            <img src={worldSkills2019TianJinImageHover} className="worldSkills2019TianJinImageHover"></img>
                        </div>
                        <div className="worldSkills2018BkkContainer">
                            <img src={worldSkills2018BkkImageHover} className="worldSkills2018BkkImageHover"></img>
        </div>*/}
                    </div>

                    </div>
            </div>
            
           
            
        )

    }

}

export default ImageHover;