import React from 'react'
import { Helmet } from 'react-helmet';
import { Container, Row, Col } from 'react-bootstrap';
import '../../App.css';
import YouTube from 'react-youtube';

function About() {
    const opts = {
      height: '400',
      width: '640',
      playerVars: {
        // https://developers.google.com/youtube/player_parameters
        modestbranding: 1,
        autoplay: 0,
      },
    };
    return (
      <>
        <Helmet>
          <title>About Us | Ngee Ann Polytechnic</title>
        </Helmet>
        <Container fluid className="header-wrap">
          <Row className="about-header-banner-logo">
            <Container className="about-header-banner-text">
              <h1>About Us</h1>
            </Container>
          </Row>
          <Row className="about-us-body">
            <Col xs={12} md={12} className="tour">
              <h2>Virtual Tour</h2>
              <Container className="tour-container">
                <YouTube videoId="yfh5CSL_ETI" className="responsive-iframe" containerClassName="video" opts={opts} />
              </Container>
            </Col>
            <Col xs={12} md={12} className="welcome">
              <Col xs={12} md={7} className="welcome-container">
                <h2>Welcome to School of Engineering!</h2>
                <p>
                  This is a Final Year Project that has been done by 3 of our aspiring Final Year students from their respective diploma programmes offered here in the School of Engineering.
                </p>
                <p>
                I am currently leading this team in development of this website. We hope that you have enjoyed our website and have learnt more about SoE.</p>
                <p>
                  We also warmly welcome you to join us in our virtual open house from 7 to 9 January 2021.
                </p>
              </Col>
              <Col xs={12} md={5} className="welcome-circle-container">
                <div className="circle koh-jun-xian-jaymeson-circle"></div>
                <h3>Koh Jun Xian Jaymeson</h3>
                <h4>Project Lead</h4>
                <h6>Diploma in Aerospace Electronics with minor in Business Management</h6>
              </Col>
            </Col>
            <Col xs={12} md={12} className="team">
              <h2>Meet The Team</h2>
              <Row className="team-container">
                <Col xs={12} md={4} className="nakagawa-miho-sherry-container">
                  <Container className="team-circle">
                    <div className="circle nakagawa-miho-sherry-circle"></div>
                  </Container>
                  <Container className="team-content">
                    <h3>Nakagawa Miho Sherry</h3>
                    <h6>Diploma in Electronics and Computer Engineering</h6>
                    <h7>Developer for Home page and World Skills</h7>
                    <p>Passionate about all things related to Coffee and Code</p>
                  </Container>
                </Col>
                <Col xs={12} md={4} className="koh-jun-xian-jaymeson-container">
                  <Container className="team-circle">
                    <div className="circle koh-jun-xian-jaymeson-circle"></div>
                  </Container>
                  <Container className="team-content">
                    <h3>Koh Jun Xian Jaymeson</h3>
                    <h6>Diploma in Aerospace Electronics with Minor in Business Management</h6>
                    <h7>Developer for Web Layout, Student Life, About Us</h7>
                    <p>Loves fiddling with code in his free time and exploring the unknowns to pique his curiousity</p>
                  </Container>
                </Col>
                <Col xs={12} md={4} className="kugan-elamaran-container">
                  <Container className="team-circle">
                    <div className="circle kugan-elamaran-circle"></div>
                  </Container>
                  <Container className="team-content">
                    <h3>Kugan S/O Elamaran</h3>
                    <h6>Diploma in Electronics and Computer Engineering</h6>
                    <h7>Developer for Courses</h7>
                    <p>Responsibilities stuff..</p>
                  </Container>
                </Col>
                <Col xs={12} md={4} className="raymond-lam-sk-container">
                  <Container className="team-circle">
                    <div className="circle raymond-lam-sk-circle"></div>
                  </Container>
                  <Container className="team-content">
                    <h3>Mr. Raymond Lam S.K.</h3>
                    <h6>Senior Lecturer | School of Engineering</h6>
                    <h7>Supervisor</h7>
                  </Container>
                </Col>
              </Row>
            </Col>
          </Row>
        </Container>
      </>
    )
}

export default About
